<template>
  <v-layout class="c-bg-login">
    <v-img :src="images.bg_login" class="c-bg-login" alt="hinh anh">
      <v-flex class="c-form-login" xs6 md4 offset-md4 offset-xs3 pa-5>
        <v-flex mb-3>
          <v-avatar width="100px" height="100px">
            <v-img :src="images.logo"></v-img>
          </v-avatar>
        </v-flex>
        <v-flex>
          <span
            class="c-tran-login"
          >Please! enter your email, press the button and check mail to follow instruction</span>
        </v-flex>
        <ValidationObserver ref="observer">
          <v-form slot-scope="{ invalid, validated }">
            <VTextFieldWithValidation rules="required|email" v-model="email" label="E-mail" />

            <v-flex class="c-gr-button">
              <v-btn
                class="c-btn c-btn-login mb-3"
                text
                @click.prevent="submit"
                :disabled="invalid || !validated"
              >
                <span class="c-btn-login-text">SEND</span>
              </v-btn>
            </v-flex>
          </v-form>
        </ValidationObserver>

        <v-divider class="mt-10"></v-divider>
        <span class="c-tran-login">
          go back
          <router-link class="c-tran-login font-weight-regular font-italic" to="/login">Login!</router-link>
        </span>
      </v-flex>
    </v-img>
  </v-layout>
</template>

<script>
import { ValidationObserver } from "vee-validate";
import VTextFieldWithValidation from "@/components/input/VTextFieldWithValidation";

export default {
  components: {
    ValidationObserver,
    VTextFieldWithValidation
  },

  data() {
    return {
      images: {
        bg_login: require("../../assets/login.png"),
        logo: require("../../assets/logo_1.jpg")
      },
      email: "",
      show: false
    };
  },

  methods: {
    submit() {
      const email = this.email;
      axios
        .post("/forgot_password", { email })
        .then(response => {
          console.log(response);
          this.$store.commit(
            "showSnackbar",
            {
              message: response.data.message,
              timeout: 10000,
              multiline: false,
              type: "success"
            },
            { module: "Snackbar" }
          );
        })
        .catch(error => {
          this.$store.commit(
            "showSnackbar",
            {
              message: error.response.data.message,
              timeout: 3000,
              multiline: false,
              type: "error"
            },
            { module: "Snackbar" }
          );
        });
      this.email = "";
      requestAnimationFrame(() => {
        this.$refs.observer.reset();
      });
    }
  }
};
</script>

<style lang="scss" scoped>
@import "./../../styles/main.scss";

.c-bg-login {
  display: flex;
  align-items: center;
  height: 722px;
  width: 100%;
}

.c-form-login {
  background-color: $bg-color-login;
  border-radius: 10px;
  width: 100%;
  display: block;
  text-align: center;
}

.c-gr-button {
  display: grid;
  justify-content: center;
  margin-top: 10px;
}

.c-btn {
  border-radius: 20px;
}

.c-btn-login {
  background-color: $bg-color-btn-login;
}
.c-btn-face {
  background-color: $bg-color-btn-face;
}

.c-btn-login-text,
.c-btn-face-text {
  padding: 5px 60px;
  color: $color-text;
}

.c-tran-login {
  color: $color-text;
  text-align: center;
  text-decoration: none;
}
</style>



